main {
    border-style: hidden;
}

h2 {
    color: black;
}

h3 {
    color: black;
}


a {
    color: black;
}

p {
    line-height: 1em;
    color: rgba(0, 0, 0, 0.473);
    text-align: end;
    font-size: 45px;
    font-family: 'Amatic SC', cursive;
    box-shadow: 8px 5px 5px lightslategray;
}

img:hover {
  box-shadow: 0 0 2px 1px rgba(0, 140, 186, 0.5);
}

#grayscale {
  
  border-radius: 50%;
  box-shadow: 5px 5px 5px lightgrey;
  /* Add the blur effect */
  filter: blur(8px);
  -webkit-filter: blur(10px);
}
#grayscale:hover {
filter: blur(0);
  -webkit-filter: blur();
}

hr {
  
  border: 0; 
  height: 1px; 
  background-image: linear-gradient(to right, #f0f0f0, #00b9ff, #59d941, #f0f0f0);
  box-shadow: 5px 5px 5px lightcoral;
}
footer {
  background: linear-gradient(to right, white, lightgray);
  -webkit-animation: gradient 15s ease infinite;
          animation: gradient 15s ease infinite;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

footer p {
  text-align: center;
  font-family: "Amatic SC";
}




.zoomOut {
  position: relative;
  width: 100%;
}
h2 {
  font-family: "Amatic SC";
  
}

img {
  
  display: block;
  width: 100%;
  height: auto;
  border-radius: 3%;
 
box-shadow: 15px 8px 8px 4px rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.overlay {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgb(182, 204, 101);
  overflow: hidden;
  width: 100%;
  height: 100%;
  -webkit-transform: scale(0);
  transform: scale(0);
  transition: .4s ease;
  border-radius: 3%;
  
  
}

.zoomOut:hover .overlay {
  -webkit-transform: scale(1);
  transform: scale(1);
  opacity: .9;
  
}

.text1 {
  position: absolute;
  top: 20%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  text-align: center;
  font-size: 1.5rem;
  font-weight: 700;
}
.text2  {
  position: absolute;
  font-size: 1em;
  font-weight: bold;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  text-align: center;
}

.text3 {
  position: absolute;
  top: 80%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  text-align: center;
  font-size: 1em;
  
}

h3, h5, a {
    font-family: 'Amatic SC', cursive;
    font-size: 2em;
}

a i {
    color: #0077b5;
    margin-left: 10px;
}

.card {
